/* 通用样式 */
* {
  margin: 0;
  padding: 0;
  -webkit-touch-callout: none;
  box-sizing: border-box;
}
html,
body {
  height: 100%;
}
#app {
  position: relative;
  height: 100%;
  overflow: hidden;
}
.container {
  position: relative;
  width: 100%;
  height: 100%;
}
img {
  vertical-align: top;
}
.page {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.weui-toast {
  font-size: 16px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
ul,
li {
  list-style: none;
}
img {
  vertical-align: top;
}
.beyondOneLine {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.beyondTwoLine {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.weui-dialog {
  font-size: 24px;
}
/* 全局自定义样式 */


